.all-destinations-area {
    position: relative;

    .route-time-line {
        max-height: 44vh;
        overflow-y: auto;
        .fire-ring {
            position: absolute;
            height: 36px;
            width: 35px;
            top: 10px;
            right: 20px;
            border-radius: 50%;
            &.on {
                box-shadow: 0 0 4px red;
                animation: fire 1.5s ease-in-out infinite;
            }
            @keyframes fire {
                0% {
                    box-shadow: 0 0 4px red;
                }
                50% {
                    box-shadow: 0 0 15px red;
                }
                100% {
                    box-shadow: 0 0 4px red;
                }
            }
        }

        .line {
            border-left: 3px dotted #7d7d7d;
            opacity: 0.5;
            height: 35px;
            margin-inline-start: 19px;
        }
        .index-ball {
            cursor: grab;
            color: rgb(253, 253, 253);
            height: 40px;
            width: 40px;
            border-radius: 50%;
            box-shadow: 0px 3px 7px #00000045;
            margin: 5px 10px 5px 0;
            transition: transform 0.2s;
            &.is-dragging {
                // transform: scaleX(1.5) scaleY(1.5);
                transform: translateX(50px) scaleX(0.5) scaleY(0.5);
            }
        }
        .visi-none {
            visibility: hidden;
        }

        .time-line-area {
            .time-line-icons {
                height: 100%;
                margin-right: 5px;
            }
        }
        .route-time-line-context {
            .time-line-text {
                p:nth-child(1) {
                    text-transform: capitalize;
                    font-weight: bold;
                    line-height: 1.5;
                    color: #333333;
                }
                p:nth-child(2) {
                    font-size: 0.8rem;
                    letter-spacing: 0.8px;
                    color: #b0b0b0;
                }
            }
        }
    }
}
